.delivery-payment {
	position: relative;
	margin-bottom: 70px;
	&__decoration {
		position: absolute;
		svg {
			width: 100%;
			height: 100%;
		}
		&_1 {
			width: 90px;
			height: 203px;
			bottom: -70px;
			right: 60px;
		}
		&_2 {
			width: 203px;
			height: 90px;
			top: 0;
			left: 0;
		}
	}
	.section-title {
		margin-bottom: 35px;
		padding-bottom: 0;
	}
	@include _mobile-tablet {
		&__decoration {
			display: none;
		}
	}
	@include _mobile {
		margin-bottom: 18px;
		.section-title {
			margin-bottom: 20px;
		}
	}
	@include _tablet {
		margin-bottom: 20px;
		.section-title {
			margin-bottom: 10px;
			padding-bottom: 0;
		}
	}
}
.delivery {
	margin-bottom: 35px;
	&-card {
		background-color: #f2f2f2;
		border-radius: 20px;
		min-height: 200px;
		padding: 23px 42px 26px 16px;
		.title-3 {
			margin-bottom: 13px;
		}
		&__text {
			margin-bottom: 16px;
			@extend %paragraph-1;
			color: #000000;
		}
		&__icon {
			width: 168px;
			height: 52px;
			svg {
				width: 100%;
				height: 100%;
			}
		}
	}
	@include _mobile {
		margin-bottom: 5px;
		&-card {
			min-height: 140px;
			margin-bottom: 17px;
			padding: 13px 11px 18px 14px;
			.title-3 {
				margin-bottom: 5px;
			}
			&__icon {
				width: 91px;
				height: 28px;
			}
			&__text {
				margin-bottom: 5px;
			}
		}
	}
	@include _tablet {
		&-card {
			min-height: 150px;
			padding: 14px 12px 20px 16px;

			&__icon {
				width: 101px;
				height: 31px;
			}
			&__text {
				margin-bottom: 10px;
			}
			.title-3 {
				margin-bottom: 5px;
			}
		}
	}
}

.payment {
	.title-3 {
		margin-bottom: 17px;
	}
	&-card {
		background: #f2f2f2;
		border-radius: 20px;
		display: flex;
		align-items: center;
		justify-content: flex-start;
		padding: 10px 10px 10px 20px;
		min-height: 80px;
		&__icon {
			width: 40px;
			min-width: 40px;
			height: 40px;
			margin-right: 14px;
			svg {
				width: 100%;
				height: 100%;
				color: $yellow;
			}
		}
		&__text {
			@extend %paragraph-1;
			line-height: 1.21;
		}
	}
	@include _mobile {
		&-card {
			margin-bottom: 12px;
			min-height: 70px;
		}
	}
	@include _tablet {
		&-card {
			margin-bottom: 30px;
		}
		.title-3 {
			margin-bottom: 30px;
		}
	}
}
