.callback {
	background-repeat: no-repeat;
	background-position: center center;
	background-size: cover;
	padding: 40px 0;
	&__form .input {
		margin-bottom: 20px;
	}
	.title-1 {
		color: #fff;
		text-align: left;
		margin-bottom: 24px;
	}
	&_small {
		position: relative;
		padding: 30px;
		width: calc(100% + 30px);
		left: -15px;
		.title-1 {
			margin-bottom: 0;
		}
		.callback__form {
			display: flex;
			align-items: center;
			justify-content: space-between;
			.input {
				margin-bottom: 0;
				max-width: 350px;
				width: 100%;
			}
		}
	}

	@include _mobile {
		.title-1 {
			margin-bottom: 10px;
		}
		&_small {
			padding: 30px 20px 30px 14px;
			.title-1 {
				line-height: 20px;
			}
		}
		.callback__form {
			flex-direction: column;
			.input {
				margin-bottom: 10px;
			}
			.btn-primary {
				width: 100%;
			}
		}
	}
	@include _tablet {
		&_small {
			padding: 50px 20px;
			.callback__form .input {
				max-width: 190px;
			}
		}
		.btn-primary {
			width: 100%;
		}
	}
}
