.gallery {
	&__big {
		width: 350px;
		height: 313px;
	}
	// &__big-item {
	// }
	&__thumbs {
		max-width: 225px;
		width: 100%;
		margin-right: auto;
		margin-left: auto;
		.owl-nav {
			width: calc(100% + 120px);
			left: -60px;
			top: 50%;
			button {
				width: 50px;
				height: 50px;
				border-radius: 6px;
				background-color: #fff !important;
				border: 1px solid #c4c4c4 !important;
				transition: 0.3s all;
				svg {
					color: #626262;
				}
				&:hover {
					background-color: $yellow !important;
					border-color: $yellow !important;
					svg {
						color: #fff;
					}
				}
			}
		}
	}
	&__thumbs-item {
		width: 63px;
		height: 60px;
		margin-right: auto;
		margin-left: auto;
		cursor: pointer;
	}
	@include _mobile {
		&__big {
			width: 100%;
			max-width: 320px;
			height: auto;
			margin-left: auto;
			margin-right: auto;
			margin-bottom: 5px;
		}
		&__thumbs {
			max-width: 185px;
			margin-bottom: 35px;
			.owl-nav {
				left: -48px;
				width: calc(100% + 96px);
				button {
					width: 41px;
					height: 41px;
				}
			}
		}
		&__thumbs-item {
			width: 53px;
			height: 50px;
		}
	}
	@include _tablet {
		&__thumbs {
			margin-left: 60px;
		}
	}
}
