.category-section-card {
	background-color: #fbfbfb;
	display: flex;
	flex-direction: column;
	text-decoration: none;
	position: relative;
	z-index: 2;
	margin-bottom: 30px;
	&__img {
		mix-blend-mode: multiply;
		display: flex;
		align-items: center;
		justify-content: center;
		min-height: 200px;
	}
	&__title {
		min-height: 74px;
		max-height: 74px;
		overflow: hidden;
		padding: 0 20px;
		@extend %card-title-1;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
		background-color: $yellow;
		text-align: center;
		color: #fff;
		border-radius: 0 0 10px 10px;
		transition: 0.3s all;
	}
	&:hover & {
		&__title {
			background-color: $red;
		}
	}

	@include _mobile {
		margin-bottom: 18px;
		max-width: 250px;
		margin-right: auto;
		margin-left: auto;
		&__title {
			font-weight: bold;
			font-size: 12px;
			line-height: 14px;
			/* or 117% */
			text-align: center;
			color: #ffffff;
			padding: 4px 2px;
			min-height: 34px;
			max-height: 34px;
			&_big {
				min-height: 50px;
				max-height: 50px;
			}
		}
		&__img {
			height: 75px;
			min-height: auto;
			img {
				height: 70%;
			}
		}
	}
	@include _tablet {
		margin-bottom: 22px;
		&__title {
			min-height: 44px;
			max-height: 44px;
			padding: 8px 12px 8px 16px;
		}
		&__img {
			height: 117px;
			min-height: auto;
			img {
				height: 70%;
			}
		}
	}
}
