.about {
	margin-bottom: 60px;
	position: relative;
	&__img {
		height: 220px;
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		margin-bottom: 30px;
		&_small {
			height: 370px;
		}
		&_decoration {
			position: relative;
			z-index: 1;
			&::after {
				content: '';
				width: 90px;
				height: 260px;
				border: 1px solid $yellow;
				z-index: -1;
				position: absolute;
				left: -15px;
				top: -10px;
			}
		}
	}
	&__title {
		font-weight: 900;
		font-size: 70px;
		line-height: 82px;
		text-align: center;
		color: #ffffff;
		position: absolute;
		z-index: 2;
	}
	.section-title {
		margin-bottom: 37px;
	}
	p {
		font-size: 20px;
		line-height: 1.8;
		/* or 180% */
		color: $black-text;
	}
	&__decoration {
		position: absolute;
		svg {
			width: 100%;
			height: 100%;
		}
		&_1 {
			width: 90px;
			height: 203px;
			bottom: -60px;
			right: 60px;
		}
		&_2 {
			width: 203px;
			height: 90px;
			top: 0;
			left: 0;
		}
	}
	.ul-lines {
		margin-bottom: 20px;
		li {
			font-size: 20px;
		}
	}
	@include _mobile-tablet {
		&__decoration {
			display: none;
		}
	}
	@include _mobile {
		margin-bottom: 15px;
		.section-title {
			margin-bottom: 20px;
			padding-bottom: 0;
		}
		&__title {
			font-size: 30px;
		}
		&__img {
			height: 94px;
			position: relative;
			margin-bottom: 15px;
			img {
				height: 100%;
				position: absolute;
				max-width: none;
				width: calc(100% + 30px);
				left: -15px;
			}
			&_small {
				height: 193px;
				margin-bottom: 20px;
				img {
					height: 100%;
				}
			}
			&_decoration::after {
				width: 48px;
				height: 134px;
				left: -8px;
				top: -5px;
			}
		}
		p {
			font-weight: bold;
			font-size: 14px;
			line-height: 19px;
			margin-top: 0;
			margin-bottom: 10px;
			/* or 139% */
		}
		.ul-lines {
			li {
				font-size: 14px;
				line-height: 19px;
			}
		}
	}
	@include _tablet {
		margin-bottom: 20px;
		&__img {
			position: relative;
			width: 100%;
			height: 150px;
			margin-bottom: 44px;
			img {
				position: absolute;
				left: -50px;
				width: calc(100% + 100px);
				height: 100%;
				object-fit: cover;
				max-width: none;
			}
			&_small {
				height: 220px;
				margin-bottom: 10px;
				img {
					height: 100%;
					width: 100%;
					object-fit: cover;
				}
			}
			&_decoration::after {
				width: 55px;
				height: 154px;
				top: -6px;
				left: -9px;
			}
		}
		&__title {
			font-size: 48px;
		}
		p {
			margin-top: 0;
			font-size: 16px;
			line-height: 19px;
			margin-bottom: 25px;
		}
		.ul-lines {
			li {
				font-size: 16px;
			}
		}
		.section-title {
			padding: 0;
			margin-bottom: 11px;
		}
	}
	@include _laptop {
		&__decoration {
			display: none;
		}
	}
}
