.blog-card {
	background-color: #f2f2f2;
	border-radius: 20px;
	overflow: hidden;
	display: flex;
	align-items: flex-start;
	justify-content: flex-start;
	margin-bottom: 30px;
	&__img {
		min-width: 350px;
		display: flex;
		align-items: center;
		justify-content: center;
		position: relative;
	}
	&__date {
		@extend %date-sticker;
		position: absolute;
		top: 22px;
		left: 0;
	}
	&-text {
		padding: 31px 65px 26px 29px;
		.title-3 {
			margin-bottom: 22px;
		}
		p {
			@extend %paragraph-1;
			margin-top: 0;
			margin-bottom: 22px;
		}
	}
	@include _mobile {
		margin-bottom: 12px;
		&__img {
			max-width: 90px;
			min-width: 90px;
		}
		&__date {
			font-size: 10px;
			line-height: 21px;
		}
		&-text {
			padding: 14px 10px 26px 10px;
			height: 100%;
			.title-3 {
				margin-bottom: 12px;
				line-height: 21px;
			}
			p {
				display: none;
				opacity: 0;
				visibility: hidden;
			}
			.btn-primary {
				max-width: 135px;
				padding: 14.5px 10px;
			}
		}
	}
	@include _tablet {
		width: fit-content;
		&__img {
			min-width: 210px;
			max-width: 210px;
			height: 100%;
		}
		&-text {
			max-width: 420px;
			padding: 23px 30px;
			.title-3 {
				margin-bottom: 12px;
			}
			p {
				margin-bottom: 12px;
			}

			.btn-primary {
				max-width: 210px;
				padding: 14.5px 10px;
			}
		}
	}
}
