.blog {
	margin-bottom: 40px;
	position: relative;
	.section-title {
		margin-bottom: 36px;
	}
	&__decoration {
		position: absolute;
		svg {
			width: 100%;
			height: 100%;
		}
		&_1 {
			width: 90px;
			height: 203px;
			bottom: -40px;
			right: 60px;
		}
		&_2 {
			width: 203px;
			height: 90px;
			top: 0;
			left: 0;
		}
	}
	@include _mobile-tablet {
		&__decoration {
			display: none;
		}
	}
	@include _mobile {
		margin-bottom: 18px;
		.section-title {
			margin-bottom: 20px;
			padding-bottom: 0;
		}
	}
	@include _tablet {
		margin-bottom: 20px;
		.section-title {
			margin-bottom: 10px;
			padding-bottom: 0;
		}
	}

	@include _laptop {
		&__decoration {
			display: none;
		}
	}
}
