// ====================================
%ul-reset {
	margin: 0;
	padding: 0;
	list-style: none;
}

%ul-lines {
	@extend %ul-reset;
	li {
		font-weight: normal;
		font-size: 15px;
		line-height: 1.5;
		/* or 150% */
		color: $black-text;
		position: relative;
		padding-left: 20px;
		&::after {
			content: '';
			width: 13px;
			height: 2px;
			background-color: $yellow;
			position: absolute;
			left: 0;
			top: 10px;
		}
	}
	@include _mobile {
		li {
			font-size: 12px;
			line-height: 1.5;
		}
	}
	@include _tablet {
		li {
			line-height: 1.25;
		}
	}
}
%ul-check {
	@extend %ul-reset;
	li {
		font-weight: normal;
		font-size: 15px;
		line-height: 1.5;
		/* or 150% */
		color: $black-text;
		// padding-left: 20px;
		&::before {
			content: '';
			width: 12px;
			height: 12px;
			display: inline-flex;
			background-position: center center;
			background-size: 100% 100%;
			background-repeat: no-repeat;
			background-image: url(../img/check.svg);
			left: 0;
			top: 10px;
			margin-right: 5px;
		}
	}

	@include _mobile {
		li {
			font-weight: normal;
			font-size: 12px;
			line-height: 1.5;
			/* or 146% */
		}
	}
	@include _tablet {
		li {
			line-height: 1.25;
		}
	}
}
%ol-main {
	counter-reset: olCounter;
	padding: 0;
	margin: 0;
	list-style: none;
	li {
		font-weight: normal;
		font-size: 14px;
		line-height: 1.5;
		margin-bottom: 30px;
		&:last-child {
			margin-bottom: 0;
		}
		&::before {
			counter-increment: olCounter;
			content: counter(olCounter) '.';
			font-weight: bold;
			font-size: 14px;
			line-height: 1.5;
			/* or 150% */
			font-feature-settings: 'pnum' on, 'lnum' on;
			color: $yellow;
		}
	}
}

%date-sticker {
	background: $yellow;
	border-radius: 0px 50px 50px 0px;
	@extend %blog-date;
	text-transform: uppercase;
	font-feature-settings: 'pnum' on, 'lnum' on;
	padding: 2px 10px;
	color: #ffffff;
}

%backdrop {
	background: rgba(0, 0, 0, 0.5);
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 10;
	display: none;
}
