.heating {
	margin-bottom: 70px;
	position: relative;
	.section-title {
		margin-bottom: 35px;
	}
	&__img {
		height: 370px;
		margin-bottom: 35px;
		&_2 {
			height: 420px;
		}
		&_decoration {
			position: relative;
			z-index: 1;
			&:after {
				content: '';
				position: absolute;
				width: 91px;
				height: 330px;
				border: 1px solid $yellow;
				top: -13px;
				left: -15px;
				z-index: -1;
			}
		}
	}
	p {
		margin-bottom: 30px;
		margin-top: 0;
		&:last-child {
			margin-bottom: 0;
		}
	}
	ul {
		@extend %ul-reset;
		margin-bottom: 30px;
		li {
			@extend %paragraph-1;
			margin-bottom: 9px;
			&:last-child {
				margin-bottom: 0;
			}
			&::before {
				content: '-';
				color: $yellow;
			}
		}
	}
	.title-3 {
		margin-bottom: 15px;
	}
	.title-5 {
		margin-bottom: 20px;
	}
	.title-6 {
		margin-bottom: 11px;
	}
	ol {
		@extend %ol-main;
		margin-bottom: 60px;
	}
	.title_thin {
		max-width: 360px;
	}
	.ul_margin {
		li {
			margin-bottom: 20px;
		}
	}
	&__decoration {
		position: absolute;
		svg {
			width: 100%;
			height: 100%;
		}
		&_1 {
			width: 90px;
			height: 203px;
			bottom: 15%;
			right: 60px;
		}
		&_2 {
			width: 203px;
			height: 90px;
			top: 50%;
			left: 0;
		}

		&_4 {
			width: 90px;
			height: 203px;
			top: 40%;
			left: 60px;
		}
		&_3 {
			width: 203px;
			height: 90px;
			bottom: 35%;
			right: 0;
		}
	}
	@include _mobile-tablet {
		&__decoration {
			display: none;
		}
	}
	@include _mobile {
		margin-bottom: 22px;
		.section-title {
			padding-bottom: 0;
			margin-bottom: 20px;
		}
		&__img {
			height: auto;
			margin-bottom: 10px;
			&_decoration:after {
				width: 24px;
				height: 85px;
				top: -3px;
				left: -3px;
			}
		}
		p {
			margin-bottom: 10px;
			&:last-child {
				margin-bottom: 10px;
			}
		}
		ol {
			margin-bottom: 10px;
			li {
				margin-bottom: 10px;
			}
		}
		.ul_margin {
			margin-bottom: 0;
			li {
				margin-bottom: 10px;
			}
		}
	}
	@include _tablet {
		margin-bottom: 5px;
		.section-title {
			margin-bottom: 10px;
			padding-bottom: 0;
		}
		&__img {
			margin-bottom: 24px;
			height: auto;
			height: 250px;
			img {
				height: 100%;
				width: auto;
			}
			&_decoration::after {
				width: 55px;
				height: 200px;
			}
		}
		p {
			margin-bottom: 16px;
		}
		ol {
			margin-bottom: 40px;
			li {
				margin-bottom: 20px;
			}
		}
	}
}
