.article {
	position: relative;
	&__img {
		position: relative;
		margin-bottom: 30px;
		height: 220px;
		&_small {
			height: 380px;
			margin-bottom: 60px;
		}
		&_decoration-left,
		&_decoration-right {
			position: relative;
			z-index: 1;
			&::after {
				content: '';
				position: absolute;
				width: 90px;
				height: 260px;
				border: 1px solid $yellow;
				top: -10px;
				z-index: -1;
			}
		}
		&_decoration-left:after {
			left: -16px;
		}
		&_decoration-right:after {
			right: -16px;
		}
	}
	&__date {
		@extend %date-sticker;
		position: absolute;
		top: 30px;
		left: 0;
	}
	.section-title {
		margin-bottom: 37px;
	}
	p {
		font-weight: normal;
		font-size: 14px;
		line-height: 1.5;
		/* or 150% */

		color: #000000;
		margin-bottom: 30px;
	}
	.ul-item__yellow {
		color: $yellow;
		font-weight: bold;
	}

	ul li {
		margin-bottom: 20px;
		position: relative;
		padding-left: 22px;
		&:before {
			position: absolute;
			left: 0;
			top: 5px;
		}
	}
	.ul-check_yellow {
		li {
			color: $yellow;
		}
	}
	.title-3 {
		margin-bottom: 20px;
	}

	&__decoration {
		position: absolute;
		svg {
			width: 100%;
			height: 100%;
		}
		&_1 {
			width: 90px;
			height: 203px;
			bottom: 20%;
			right: 60px;
		}
		&_2 {
			width: 203px;
			height: 90px;
			top: 0;
			left: 0;
		}
	}
	@include _mobile-tablet {
		&__decoration {
			display: none;
		}
	}

	@include _mobile {
		margin-top: 25px;
		&__img {
			height: auto;
			margin-bottom: 13px;
			min-height: 76px;
			height: 76px;
			&_decoration-left,
			&_decoration-right {
				&::after {
					width: 47px;
					height: 134px;
				}
			}
			&_decoration-left::after {
				left: -8px;
			}
			&_decoration-right::after {
				right: -8px;
			}
			img {
				height: 100%;
				object-fit: cover;
			}
			&_small {
				height: 212px;
				margin-bottom: 20px;
				img {
					height: 100%;
					object-fit: cover;
				}
			}
		}
		.section-title {
			margin-bottom: 13px;
			padding-bottom: 0;
		}
		p {
			font-size: 12px;
			margin-bottom: 15px;
		}
		ul li {
			margin-bottom: 12px;
		}
	}
	@include _tablet {
		.section-title {
			margin-bottom: 10px;
			padding-bottom: 0;
		}
		.title-3 {
			margin-bottom: 12px;
		}
		&__img {
			height: auto;
			margin-bottom: 20px;
			&_small {
				width: fit-content;
				height: auto;
				margin-bottom: 10px;
			}
		}
		p {
			margin-top: 0;
			margin-bottom: 10px;
		}

		ul li {
			margin-bottom: 10px;
		}
		&_decoration-left,
		&_decoration-right {
			position: relative;
			z-index: 1;
			&::after {
				width: 58px;
				height: 164px;
			}
		}
		// &
	}
}
