.hero {
	min-height: 500px;
	position: relative;
	margin-bottom: 70px;
	.container {
		position: relative;
	}
	&__decoration {
		position: absolute;
		z-index: 2;
		&_1 {
			width: 139px;
			height: 60px;
			top: 50px;
			left: 60%;
			svg {
				width: 100%;
				height: 100%;
			}
		}
		&_2 {
			height: 133px;
			width: 203px;
			bottom: 0;
			right: 0;
			svg {
				width: 100%;
				height: 100%;
			}
		}
	}

	@include _mobile {
		min-height: 180px;
		margin-bottom: 30px;
		&__decoration {
			&_1 {
				width: 23px;
				height: 16px;
				top: 10%;
			}
			&_2 {
				width: 33px;
				height: 34px;
			}
		}
	}

	@include _tablet {
		min-height: 250px;
		margin-bottom: 40px;
		&__decoration {
			&_1 {
				width: 55px;
				height: 24px;
			}
			&_2 {
				width: 80px;
				height: 47px;
			}
		}
		.container {
			display: flex;
			align-items: center;
			justify-content: flex-start;
		}
	}
}

.hero-item {
	padding-top: 95px;
	display: flex;
	align-items: flex-start;
	justify-content: flex-start;
	min-height: 500px;
	height: 500px;
	position: relative;
	z-index: 1;
	&__bg {
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		z-index: -1;
		img {
			height: 100% !important;
			width: 100% !important;
			object-fit: cover;
		}
	}
	&__text {
		margin-top: 40px;
	}
	&__title {
		@extend %hero-title;
		margin-top: 0;
		max-width: 670px;
		margin-bottom: 22px;
		text-align: left;
		.red {
			color: $red;
		}
		&_2 {
			max-width: 490px;
		}
	}
	&__subtitle {
		@extend %subtitle-1;
		max-width: 570px;
		margin-bottom: 15px;
	}
	&__img {
		position: absolute;
		top: -65px;
		right: -30px;
		&_top {
			top: -95px;
			z-index: 2;
		}
	}
	@include _mobile {
		min-height: 180px;
		height: 180px;
		padding-top: 24px;

		.container {
			display: flex;
			align-items: center;
			justify-content: flex-start;
			&_no-relative {
				position: static;
			}
		}
		&__bg {
		}
		&__img {
			// display: none;
			width: 147px;
			height: 132px;
			left: 170px;
			top: 0;
			right: auto;
			&_top {
				top: -24px;
			}
			&_bottom {
				bottom: 0;
				top: auto;
			}
			&_small {
				width: 100px;
				height: 100px;
				left: 200px;
			}
			// position: static;
		}
		&__title {
			margin-bottom: 5px;
			&_2 {
				max-width: 150px;
			}
			&_3 {
				max-width: 180px;
			}
		}
		&__text {
			max-width: 200px;
			margin-top: 0;
		}
		&__subtitle {
			margin-bottom: 10px;
		}
	}
	@include _tablet {
		min-height: 250px;
		height: 250px;
		padding-top: 40px;
		&__title {
			max-width: 310px;
			margin-bottom: 10px;
			&_2 {
				max-width: 200px;
			}
		}
		&__subtitle {
			max-width: 280px;
		}
		&__text {
			margin-left: 100px;
			width: fit-content;
			margin-top: 0;
		}
		&__img {
			width: 220px;
			height: 182px;
			top: 0;
			right: 0;
			position: relative;
			&_top {
				position: absolute;
				top: -40px;
				right: 100px;
			}
		}
	}
}
