.manufacturer {
	margin-bottom: 100px;
	position: relative;
	.section-title {
		margin-bottom: 23px;
	}
	.col-lg-3 {
		position: relative;
	}
	&__decoration {
		position: absolute;
		svg {
			width: 100%;
			height: 100%;
		}
		&_1 {
			width: 90px;
			height: 203px;
			bottom: -100px;
			right: 60px;
		}
		&_2 {
			width: 203px;
			height: 90px;
			top: 0;
			left: 0;
		}
	}
	&_grey {
		background-color: #fbfbfb;
		padding: 65px 0;
		margin-bottom: 0;
		.section-title {
			margin-bottom: 70px;
		}
		.manufacturer__decoration {
			display: none;
		}
	}

	@include _mobile-tablet {
		&__decoration {
			display: none;
		}
	}
	@include _mobile {
		margin-bottom: 30px;
		&_grey {
			padding: 16px 0 30px 0;
			margin-bottom: 30px;
		}
		.section-title {
			margin-bottom: 12px;
		}
	}
	@include _tablet {
		margin-bottom: 50px;
		.section-title {
			margin-bottom: 26px;
		}
		&_grey {
			padding: 38px 0 50px 0;
		}
	}
}

.manufacturer-card {
	mix-blend-mode: multiply;
	position: relative;
	width: calc(100% + 30px);
	left: -15px;
	display: flex;
	align-items: center;
	justify-content: center;
	min-height: 106px;
	max-height: 106px;
	border-right: 1px solid $border-color;
	border-bottom: 1px solid $border-color;
	overflow: hidden;
	&_no-border {
		border-right: none;
	}
	&_no-border-bottom {
		border-bottom: none;
	}
	&_left-border {
		border-left: 1px solid $border-color;
	}

	@include _mobile-tablet {
		width: calc(100% + 16px);
		left: -8px;
	}
	@include _mobile {
		min-height: 30px;
		max-height: 30px;
		img {
			width: 60%;
			// height: auto !important;
		}
	}
	@media (min-width: 420px) and (max-width: 639px) {
		min-height: 50px;
		max-height: 50px;
	}

	@media (min-width: 640px) and (max-width: 767px) {
		min-height: 60px;
		max-height: 60px;
	}
	@include _tablet {
		min-height: 64px;
		max-height: 64px;
		img {
			width: 60%;
		}
	}
}
