.product-page {
	position: relative;
	.section-title {
		margin-bottom: 50px;
	}
	&__gallery {
		margin-bottom: 60px;
	}
	&__description {
		margin-bottom: 46px;
		.title-3 {
			margin-bottom: 30px;
		}
		p {
			margin-top: 0;
			font-size: 16px;
			line-height: 21px;
			font-weight: 400;
			&:last-child {
				margin-bottom: 0;
			}
		}
	}
	&__characteristic.title-3 {
		margin-bottom: 23px;
	}
	&__characteristic-table {
		max-width: 570px;
		width: 100%;
		margin-bottom: 90px;
	}

	&__decoration {
		position: absolute;
		svg {
			width: 100%;
			height: 100%;
		}

		&_1 {
			width: 203px;
			height: 90px;
			top: 60%;
			left: 0;
		}
	}
	@include _mobile-tablet {
		&__decoration {
			display: none;
		}
		&__description {
			margin-bottom: 36px;
		}
	}

	@include _mobile {
		.section-title {
			margin-top: 30px;
			margin-bottom: 15px;
		}
		&__gallery {
			margin-bottom: 30px;
		}
		&__description {
			margin-bottom: 25px;
		}
		&__characteristic-table {
			max-width: 400px;
			margin-bottom: 20px;
		}
		&__characteristic .title-3 {
			margin-bottom: 12px;
		}
	}
	@include _tablet {
		&__characteristic .title-3 {
			margin-bottom: 36px;
		}
		&__characteristic-table {
			margin-bottom: 40px;
		}
	}
}
