.btn {
	&-primary {
		@extend %btn-text;
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: transparent;
		cursor: pointer;
		text-decoration: none;
		border: none;
		&::after {
			content: attr(title);
		}
	}
	&-size-1 {
		min-width: 190px;
		border-radius: 4px;
		width: fit-content;
		padding: 20px 10px;
	}
	&-size-2 {
		padding: 20px 15px;
		border-radius: 4px;
	}
	&-size-3 {
		padding: 20px;
		min-width: 350px;
		width: fit-content;
		border-radius: 4px;
	}
	&-size-4 {
		padding: 7.5px 10px;
		min-width: 190px;
		width: fit-content;
		border-radius: 4px;
	}
	&-size-5 {
		padding: 10px 20px;
		width: fit-content;
		min-width: 150px;
		border-radius: 4px;
	}
	&-red {
		background-color: $red;
		color: #fff;
		transition: 0.3s all;
		&:hover {
			background-color: $yellow;
		}
	}
	&-red-border {
		background-color: transparent;
		color: $red;
		border: 1px solid $red;
		transition: 0.3s all;
		&:hover {
			background-color: $red;
			color: #fff;
		}
	}
	@include _mobile {
		&-size-1 {
			min-width: 135px;
			padding: 8px 5px;
		}
		&-size-2 {
			padding: 6px 2px;
		}
		&-size-3 {
			min-width: 135px;
			padding: 9px 10px;
		}
		&-size-4 {
			width: 100%;
			min-width: auto;
			padding: 4px 10px;
		}
		&-size-5 {
			padding: 8px 10px;
			min-width: 135px;
		}
	}
	@include _tablet {
		&-size-1 {
			min-width: 208px;
			padding: 15px;
		}
		&-size-2 {
			padding: 14.5px 10px;
		}
		&-size-3 {
			min-width: 190px;
			padding: 15px;
		}
		&-size-4 {
			width: 100%;
			min-width: auto;
		}
	}
}
