.calculator {
	padding: 65px 0;
	margin-bottom: 66px;
	background-repeat: no-repeat;
	background-position: center center;
	background-size: cover;
	&_no-margin {
		margin-bottom: 0;
	}
	.title-1 {
		color: #fff;
		margin-bottom: 15px;
		text-align: left;
	}
	.subtitle-2 {
		color: #fff;
	}

	@include _mobile {
		padding: 15px 0 30px 0;
		margin-bottom: 22px;
		.title-1 {
			line-height: 1.3;
		}
		.subtitle-2 {
			margin-bottom: 20px;
		}
		.btn-primary {
			max-width: 300px;
			margin-right: auto;
			margin-left: auto;
		}
	}

	@include _tablet {
		padding: 40px 0;
	}
}
