.catalog {
	position: relative;
	.partnership-advantages {
		margin-bottom: 30px;
	}
	.section-title {
		margin-bottom: 30px;
	}
	&__heading {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-bottom: 47px;
	}
	&__result {
		margin-bottom: 92px;
		.product-card {
			margin-bottom: 30px;
		}
	}
	.pagination {
		margin-top: 40px;
	}
	&__seo {
		margin-bottom: 90px;
		.title-3 {
			margin-bottom: 25px;
			max-width: 590px;
		}
		p {
			margin-top: 0;
			&:last-child {
				margin-bottom: 0;
			}
		}
	}
	&__decoration {
		position: absolute;
		svg {
			width: 100%;
			height: 100%;
		}
		&_1 {
			width: 90px;
			height: 203px;
			bottom: 30%;
			right: 60px;
		}
		&_2 {
			width: 203px;
			height: 90px;
			top: 30%;
			left: 0;
		}
	}
	&__filter-btn {
		display: none;
		opacity: 0;
		visibility: hidden;
	}
	@include _mobile-tablet {
		&__decoration {
			display: none;
		}
	}
	@include _mobile {
		&__filter-btn {
			opacity: 1;
			visibility: visible;
			display: flex;
		}
		&__heading {
			flex-wrap: wrap;
		}
		.section-title {
			width: 100%;
			margin-top: 30px;
		}
		&__result {
			.product-card {
				margin-bottom: 10px;
			}
		}
		.pagination {
			margin-top: 35px;
		}
		&__result {
			margin-bottom: 40px;
		}

		&__seo {
			margin-bottom: 30px;
			.title-3 {
				margin-bottom: 6px;
			}
			p {
				margin-bottom: 10px;
			}
		}
	}
	@include _tablet {
		&__heading {
			justify-content: space-between;
			flex-wrap: wrap;
		}
		.section-title {
			width: 100%;
		}

		&__filter-btn {
			opacity: 1;
			visibility: visible;
			display: flex;
		}
		&__result {
			margin-bottom: 50px;
			.product-card {
				margin-bottom: 13px;
			}
		}
		&__seo {
			margin-bottom: 50px;
			p {
				line-height: 21px;
			}
		}
	}
}

.catalog-a-side {
	transform: none;
	position: relative;
	z-index: 2;
	&__filter {
		background-color: #f2f2f2;
		border-radius: 20px;
		padding: 28px 25px 40px 23px;
	}
	&__close-btn {
		display: none;
		opacity: 0;
		visibility: hidden;
	}
	@include _mobile-tablet {
		transform: translateX(-100%);
		position: fixed;
		top: 0;
		left: 0;
		z-index: 1000;
		height: 100%;
		overflow-y: auto;
		transition: 0.3s all;
		&__close-btn {
			display: flex;
			align-items: center;
			justify-content: center;
			border: none;
			background-color: transparent;
			width: 20px;
			height: 20px;
			padding: 0;
			position: absolute;
			top: 23px;
			opacity: 1;
			visibility: visible;
			right: 28px;
			cursor: pointer;

			svg {
				width: 100%;
				height: 100%;
				color: $yellow;
			}
		}
	}
	@include _mobile {
		width: 100%;
		&__filter {
			border-radius: 0;
			padding: 64px 15px 30px 15px;
		}
	}
	@include _tablet {
		&__filter {
			border-radius: 0;
			padding: 65px 50px;
		}
	}
}
