.filter {
	&__title {
		@extend %title-3;
		margin-bottom: 15px;
	}
	&__options {
		margin-bottom: 22px;
		&:last-child {
			margin-bottom: 0;
		}
		.checkbox:last-child {
			margin-bottom: 0;
		}
	}
	&__actions {
		display: none;
	}
	@include _mobile {
		&__title {
			font-size: 20px;
		}
		&__actions {
			display: flex;
			align-items: flex-start;
			justify-content: space-between;

			.btn-primary {
				padding: 8px 10px;
				border-radius: 4px;
				max-width: 135px;
				width: 100%;
				margin-bottom: 10px;
			}
		}
	}
	@include _tablet {
		&__actions {
			display: flex;
			align-items: flex-start;
			justify-content: flex-start;
			flex-direction: column;
			.btn-primary {
				padding: 15px 10px;
				border-radius: 4px;
				width: 100%;
				margin-bottom: 10px;
			}
		}
	}
}

.price-ranges {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	align-content: flex-start;
	flex-wrap: wrap;
	position: relative;
	margin-bottom: 25px;
	width: 100%;
	&__input {
		display: inline-flex;
		align-items: center;
		justify-content: flex-start;
		align-content: flex-start;
		&:first-child:after {
			content: '';
			width: 55px;
			height: 1px;
			margin: 0 5px;
			background-color: #d3d3d3;
		}
		input {
			// border: 1px solid $grey-text-color;
			outline: none;
			text-align: center;
			border: 1px solid #cecece;
			padding: 6px 10px;
			max-width: 65px;
			border-radius: 4px;
			width: 100%;
			font-weight: normal;
			font-size: 16px;
			line-height: 21px;
			/* identical to box height, or 131% */
			font-feature-settings: 'pnum' on, 'lnum' on;
			color: #000;
		}
	}

	@include _mobile {
		&__input:first-child:after {
			width: 120px;
		}
	}
}
.noUi {
	&-handle {
		top: -4px !important;
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: $yellow;
		align-content: center;
		border: none;
		right: -5px !important;
		border-radius: 50%;
		box-shadow: 0px 4px 4px rgba(18, 86, 150, 0.12);
		outline: none;
		cursor: pointer;
		width: 12px !important;
		height: 12px !important;
		&::after,
		&::before {
			content: none;
		}
	}
	&-target {
		box-shadow: none;
		border: none;
	}
	&-horizontal {
		width: 100%;
		height: 4px;
		border-radius: 4px;
		background-color: #a7a7a8;
	}
	&-connect {
		background-color: $yellow !important;
		height: 4px;
	}
}
