.termostats {
	position: relative;
	z-index: 1;
	.section-title {
		margin-bottom: 56px;
	}
	&__decoration {
		position: absolute;
		z-index: -1;
		svg {
			width: 100%;
			height: 100%;
		}
		&_1 {
			left: 0;
			bottom: 40px;
			width: 200px;
			height: 90px;
		}
	}
	@include _mobile-tablet {
		&__decoration {
			display: none;
		}
	}
	@include _mobile {
		.section-title {
			margin-bottom: 21px;
		}
	}
	@include _tablet {
		.section-title {
			margin-bottom: 37px;
		}
	}
}
