.contacts {
	margin-bottom: 70px;
	position: relative;
	.section-title {
		margin-bottom: 34px;
		text-align: left;
	}
	&__wrapper {
		display: flex;
		align-items: center;
		justify-content: flex-start;
	}
	&__tabs {
		display: flex;
		align-items: center;
		justify-content: center;
		align-content: center;
		margin-bottom: 17px;
	}
	&__tabs-item {
		display: flex;
		align-items: center;
		justify-content: center;
		text-align: center;
		width: fit-content;
		min-width: 265px;
		border: 1px solid $yellow;
		min-height: 40px;
		padding: 5px 10px;
		@extend %contacts-tabs-text;
		text-decoration: none;
		line-height: 0.93;
		margin-right: 10px;
		transition: 0.3s all;
		&:last-child {
			margin-right: 0;
		}
	}
	.active {
		background-color: $yellow;
		color: #fff;
	}
	&__tab-content {
	}
	&__map {
		height: 295px;
		display: flex;
		align-items: center;
		justify-content: center;
		iframe {
			display: block;
			width: 100%;
		}
	}

	&__decoration {
		position: absolute;
		svg {
			width: 100%;
			height: 100%;
		}
		&_1 {
			width: 90px;
			height: 203px;
			bottom: -70px;
			right: 60px;
		}
		&_2 {
			width: 203px;
			height: 90px;
			top: 0;
			left: 0;
		}
	}

	@include _mobile-tablet {
		&__decoration {
			display: none;
		}
	}
	@include _mobile {
		margin-bottom: 30px;
		&__wrapper {
			flex-direction: column;
			width: 100%;
			align-items: flex-start;
		}
		&__tabs {
			flex-direction: column;
		}
		&__tabs-item {
			margin-bottom: 10px;
			margin-right: 0;
			&:last-child {
				margin-bottom: 0;
			}
		}
		.section-title {
			margin-bottom: 33px;
			padding-bottom: 0;
		}
		.social {
			margin-bottom: 40px;
		}
		&__map {
			height: 162px;
		}
	}
	@include _tablet {
		margin-bottom: 50px;
		&__wrapper {
			width: 100%;
			justify-content: space-between;
		}
		.section-title {
			margin-bottom: 0;
			padding-bottom: 0;
		}
		.social {
			margin-bottom: 10px;
		}
		&__tabs {
			margin-bottom: 30px;
		}
		.container_inner {
			padding: 0 15px;
		}
		&__map {
			height: 178px;
		}
	}
}

.contacts-heading {
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin-bottom: 10px;
	&__item {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		text-decoration: none;
		margin-right: 22px;
		&:last-child {
			margin-right: 0;
		}
		span {
			@extend %contacts-heading-text;
			font-feature-settings: 'pnum' on, 'lnum' on;
		}
		&_mail {
			text-decoration: underline;
		}
	}
	&__icon {
		padding: 6px;
		border-radius: 50%;
		width: 23px;
		height: 23px;
		margin-right: 7px;
		display: flex;
		align-items: center;
		justify-content: center;
		&_viber {
			background-color: #665cac;
		}
		&_telegram {
			background-color: #60a8de;
		}
		svg {
			width: 10px;
			height: 10px;
			color: #fff;
		}
		&_mail {
			padding: 0;
			svg {
				color: $yellow;
				width: 14px;
				height: 14px;
			}
		}
	}
	@include _mobile-tablet {
		flex-wrap: wrap;
	}
	@include _mobile {
		&__item {
			margin-bottom: 12px;
		}
	}
	// @include _tablet {
	// 	margin-bottom: 12px;
	// }
}
