.header {
	position: sticky;
	top: 0;
	z-index: 999;
	&__notice {
		background-color: $green;
		padding: 10px 0;
		@extend %header-text-bold;
		text-align: center;
		color: #fff;
	}
	.logo {
		color: #000;
	}
	&-contacts__logo {
		display: none;
	}
	&__discount-btn {
		@extend %btn-text;
		text-decoration: none;
		border: none;
		color: #fff;
		background-color: $red;
		border-radius: 4px;
		padding: 20.5px 5px;
		min-width: 155px;
		width: fit-content;
		transition: 0.3s all;
		cursor: pointer;
		&:hover {
			background-color: $yellow;
		}
	}
	&__middle {
		padding: 22px 0;
		background-color: #fff;
		.container {
			display: flex;
			align-items: center;
			justify-content: space-between;
		}
	}
	&__bottom {
		background-color: $yellow;
		padding: 20px 0;
		.container {
			display: flex;
			align-items: center;
			justify-content: space-between;
		}
	}
	&__backdrop {
		@extend %backdrop;
	}
	&__mobile-btn {
		display: none;
		opacity: 0;
		visibility: hidden;
	}
	&__mobile-btn-close {
		display: none;
		opacity: 0;
		visibility: hidden;
	}
	@include _mobile-tablet {
		&__mobile-btn-close {
			display: flex;
			align-items: center;
			justify-content: center;
			visibility: visible;
			opacity: 1;
			width: 20px;
			height: 20px;
			position: absolute;
			top: 20px;
			right: 20px;
			background-color: transparent;
			background-color: transparent;
			padding: 0;
			border: none;
			cursor: pointer;
			svg {
				width: 100%;
				height: 100%;
				color: #fff;
			}
		}
		&__bottom {
			position: fixed;
			left: 0;
			height: 100%;
			overflow-y: auto;
			overflow-x: hidden;
			top: 0;
			z-index: 11;
			padding: 0;
			transform: translateX(-100%);
			transition: 0.3s all;
			.container {
				max-width: none;

				align-items: flex-start;
				margin: 0;
				flex-direction: column;
			}
		}
		&__mobile-btn {
			display: flex;
			align-items: center;
			justify-content: space-between;
			flex-direction: column;
			opacity: 1;
			visibility: visible;
			width: 42px;
			height: 60px;
			padding: 20px 11px;
			background-color: $yellow;
			border-radius: 4px;
			border: none;
			cursor: pointer;
			span {
				width: 20px;
				height: 4px;
				border-radius: 2px;
				background-color: #fff;
			}
		}
	}
	@include _mobile {
		.logo {
			margin-bottom: 10px;
		}
		&-contacts__logo {
			display: flex;
			align-items: flex-start;
			justify-content: flex-start;
			flex-direction: column;
			padding-left: 7px;
			.header-contacts__item {
				margin-bottom: 3px;
				span {
					font-size: 10px;
					line-height: 1;
				}
			}
			.header-contacts__icon {
				width: 10px;
				height: 10px;
				svg {
					width: 8px;
					height: 8px;
				}
			}
		}
		&__mobile-btn {
			height: 40px;
			width: 29px;
			padding: 15px 7.5px;
			span {
				height: 2px;
				width: 14px;
			}
		}
		&__discount-btn {
			min-width: 98px;
			max-width: 98px;
			padding: 5px 2px;
		}
		&__notice {
			display: none;
			opacity: 0;
			visibility: hidden;
		}
		&__middle {
			padding: 13px 0;
		}
		&__mobile-btn-close {
			width: 12px;
			min-width: 12px;
			height: 12px;
			top: 11px;
			right: 15px;
		}
		&__bottom {
			width: 100%;
			.container {
				padding-top: 31px;
				padding-bottom: 46px;
			}
		}
	}
	@include _tablet {
		&__notice {
			padding: 12px 50px;
		}
		&__middle {
			padding: 15px 0;
		}
		&__bottom {
			max-width: 280px;
			.container {
				padding: 57px 0 57px 52px;
			}
		}
	}
}
.fixed {
	box-shadow: 0px 16px 24px rgba(0, 0, 0, 0.06), 0px 2px 6px rgba(0, 0, 0, 0.04), 0px 0px 1px rgba(0, 0, 0, 0.04);

	box-shadow: 0px 16px 24px rgba(0, 0, 0, 0.06), 0px 2px 6px rgba(0, 0, 0, 0.04), 0px 0px 1px rgba(0, 0, 0, 0.04);
}
.header-info {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	&_icon {
		width: 40px;
		height: 40px;
		margin-right: 13px;
		background-color: #f2f2f2;
		border-radius: 50%;
		display: flex;
		align-items: center;
		justify-content: center;
		position: relative;
		&:after {
			content: '';
			border: 1px dashed rgba(0, 0, 0, 0.15);
			position: absolute;
			width: 32px;
			height: 32px;
			border-radius: 50%;
		}
		svg {
			width: 13px;
			height: 13px;
			color: $yellow;
		}
	}
	&__wrapper {
	}
	&__text {
		@extend %header-text;
	}
	&__caption {
		@extend %header-caption-text;
	}
	@include _mobile {
		margin-bottom: 19px;
	}
	@include _tablet {
		margin-bottom: 24px;
	}
}
.header-contacts {
	&__item {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		text-decoration: none;
		margin-bottom: 10px;
		&:last-child {
			margin-bottom: 0;
		}
		span {
			@extend %header-href;
			font-weight: normal;
			font-feature-settings: 'pnum' on, 'lnum' on;
			transition: 0.3s all;
			&:hover {
				color: $yellow;
			}
		}
	}
	&__icon {
		width: 23px;
		height: 23px;
		border-radius: 50%;
		display: flex;
		align-items: center;
		justify-content: center;
		margin-right: 7px;
		&_viber {
			background-color: #665cac;
		}
		&_telegram {
			background-color: #60a8de;
		}
		svg {
			width: 12px;
			height: 12px;
			color: #fff;
		}
	}
	&__mobile {
		display: none;
		opacity: 0;
		visibility: hidden;
	}
	@include _mobile-tablet {
		&__mobile {
			opacity: 1;
			display: flex;
			visibility: visible;
			flex-direction: column;
			margin-top: 6px;
			margin-bottom: 15px;
		}
	}

	@include _mobile {
		display: none;
		&__mobile {
			margin-top: 11px;
		}
	}
}
.header-lang {
	position: relative;
	&__toggle {
		padding: 22px 8.5px 22px 11px;
		color: $turquoise;
		@extend %header-lang;
		background-color: #f2f2f2;
		border-radius: 4px;
		cursor: pointer;
		svg {
			color: $turquoise;
			width: 12px;
			height: 6px;
			transition: 0.3s all;
		}
		&_active {
			svg {
				transform: rotate(180deg);
			}
		}
	}
	&__menu {
		position: absolute;
		opacity: 0;
		visibility: hidden;
		background-color: #f2f2f2;
		top: 100%;
		@extend %ul-reset;
		padding: 10px 6px 10px 8px;
		width: 100%;
		border-radius: 4px;
		transition: 0.3s all;
		&_active {
			opacity: 1;
			visibility: visible;
		}
	}
	&__item {
		width: 100%;
	}
	&__link {
		text-decoration: none;
		color: $turquoise;
		@extend %header-lang;
		display: flex;
		align-items: flex-start;
		justify-content: flex-start;
	}
	@include _mobile {
		&__toggle {
			width: 46px;
			padding: 22px 6px 22px 8px;
		}
	}
	@media (min-width: 1199px) {
		&:hover {
			.header-lang__toggle svg {
				transform: rotate(180deg);
			}
			.header-lang__menu {
				opacity: 1;
				visibility: visible;
			}
		}
	}
}

.header-catalog {
	position: relative;
	&__toggle {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		min-width: 240px;
		margin-right: 60px;
		position: relative;
		padding-left: 17px;
		z-index: 1;
		cursor: pointer;
		svg {
			width: 20px;
			height: 20px;
			margin-right: 9px;
		}
		span {
			@extend %header-text-2;
			color: #fff;
			text-transform: uppercase;
		}
		&::after {
			content: '';
			position: absolute;
			display: block;
			width: calc(100% + 34px);
			height: calc(100% + 40px);
			top: -20;
			left: 0;
			background-color: #e5a40a;
			z-index: -1;
		}
	}
	&__menu {
		position: absolute;
		opacity: 0;
		display: flex;
		flex-direction: column;
		visibility: hidden;
		background-color: #fff;
		z-index: 10;
		left: 0;
		top: calc(100% + 20px);
		transition: 0.3s all;
		padding: 13px;
		box-shadow: 4px 4px 8px 0px rgba(0, 0, 0, 0.2);
		max-width: 274px;
	}
	&__item {
		padding: 8px 0;
		width: 100%;
		display: block;
		font-weight: normal;
		font-size: 14px;
		line-height: 17px;
		/* or 125% */
		color: #9d9d9d;
		text-decoration: none;
		transition: 0.3s all;
		&:hover {
			color: $yellow;
		}
	}
	&:hover & {
		&__menu {
			opacity: 1;
			visibility: visible;
		}
	}

	@include _mobile-tablet {
		&__toggle {
			padding-left: 0;
			min-width: auto;
			margin-right: 0;
			&::after {
				content: none;
			}
			svg {
				display: none;
				opacity: 0;
				visibility: hidden;
			}
		}
		&__menu {
			position: relative;
			padding: 12px 0;
			opacity: 1;
			visibility: visible;
			box-shadow: none;
			max-width: 220px;
			z-index: 1;
			top: auto;
			left: auto;
			&::after {
				content: '';
				width: calc(100% + 60px);
				background-color: #fff;
				display: block;
				height: 100%;
				top: 0;
				z-index: -1;
				position: absolute;
				left: -52px;
			}
		}
		&__item {
			line-height: 27px;
			/* identical to box height, or 193% */
			padding: 4px 0;
		}
	}
	@include _mobile {
		width: 100%;
		&__toggle {
			text-align: center;
			justify-content: center;
			margin-bottom: 21px;
		}
		&__menu {
			width: 100%;
			max-width: none;
			margin-bottom: 14px;
			&::after {
				width: calc(100% + 30px);
				left: -15px;
			}
		}
		&__item {
			font-size: 12px;
		}
	}
	@include _tablet {
		&__toggle {
			margin-bottom: 17px;
		}
		&__menu {
			max-width: none;
		}
	}
}
.header-menu {
	@extend %ul-reset;
	display: flex;
	align-items: center;
	justify-content: space-between;
	width: 100%;
	&__link {
		@extend %header-text-2;
		text-decoration: none;
		text-transform: uppercase;
		color: #fff;
		width: 100%;
		&:hover {
			text-decoration: underline;
		}
	}

	@include _mobile-tablet {
		flex-direction: column;
		align-items: flex-start;
	}
	@include _mobile {
		margin-bottom: 14px;
		&__item {
			width: 100%;
		}
		&__link {
			display: block;
			padding: 4px 0;
		}
	}

	@include _tablet {
		margin-top: 12px;
		margin-bottom: 32px;
		&__item {
			width: 100%;
		}
		&__link {
			display: block;
			padding: 4px 0;
		}
	}
}
