.brand {
	position: relative;
	&__logo {
		margin-bottom: 25px;
	}
	&__img {
		margin-bottom: 50px;
	}
	&__img-decoration {
		&_right {
			position: relative;
			z-index: 1;
			&::after {
				content: '';
				position: absolute;
				width: 94px;
				height: 250px;
				border: 1px solid $yellow;
				top: -30px;
				right: -25px;
				z-index: -1;
			}
		}
		&_left {
			position: relative;
			z-index: 1;
			&::after {
				content: '';
				position: absolute;
				width: 94px;
				height: 250px;
				border: 1px solid $yellow;
				top: -15px;
				left: -21px;
				z-index: -1;
			}
		}
	}
	&__bottom {
		margin: 100px 0;
		.title-3 {
			margin-bottom: 30px;
		}
	}
	.section-title {
		margin-bottom: 31px;
	}
	.title-3 {
		margin-bottom: 30px;
	}
	p {
		font-weight: normal;
		font-size: 16px;
		line-height: 1.31;
		/* or 131% */
		color: $black-text;
		margin-top: 0;
		margin-bottom: 30px;
	}
	&__decoration {
		position: absolute;
		width: 203px;
		height: 90px;
		bottom: 40%;
		left: 0;
		svg {
			width: 100%;
			height: 100%;
		}
	}
	.partnership-advantages {
		margin-bottom: 40px;
	}
	@include _mobile-tablet {
		&__decoration {
			display: none;
		}
	}
	@include _mobile {
		margin-top: 40px;
		&__logo {
			margin-bottom: 0;
		}
		.section-title {
			font-size: 16px;
			margin-bottom: 6px;
			padding-bottom: 0;
		}
		&__img-decoration {
			&_right::after {
				width: 21px;
				height: 60px;
				top: -6px;
				right: -5px;
			}
			&_left:after {
				width: 21px;
				height: 60px;
				top: -6px;
				left: -5px;
			}
		}
		p {
			font-weight: normal;
			font-size: 12px;
			line-height: 17px;
			margin-bottom: 13px;
		}
		&__img {
			margin-bottom: 20px;
		}
		.btn-primary {
			margin-bottom: 30px;
		}
		&__bottom {
			margin-top: 30px;
			margin-bottom: 30px;
		}
	}
	@include _tablet {
		&__logo {
			margin-bottom: 8px;
		}
		&__img {
			margin-bottom: 0;
		}
		&__img-decoration_right::after {
			right: -13px;
			top: -15px;
			width: 54px;
			height: 153px;
		}
		&__img-decoration_left:after {
			width: 54px;
			height: 153px;
		}
		.section-title {
			padding-bottom: 0;
			margin-bottom: 20px;
		}

		.btn-primary {
			margin-bottom: 30px;
		}
		p {
			font-size: 14px;
			line-height: 21px;
			margin-bottom: 20px;
		}
		.title-3 {
			margin-bottom: 20px;
		}
		&__bottom {
			margin-top: 40px;
			margin-bottom: 40px;
		}
	}
}
