.breadcrumbs {
	@extend %ul-reset;
	display: flex;
	align-items: center;
	justify-content: flex-start;
	padding: 20px 0;
	li,
	li a {
		@extend %breadcrumbs-text;
		text-decoration: none;
		color: #cecece;
	}

	.active {
		color: $yellow;
	}
	@include _mobile {
		padding: 0;
		overflow-y: auto;
		li,
		li a {
			white-space: nowrap;
		}
	}
	@include _tablet {
		padding: 8px 0 30px 0;
		overflow-y: auto;
		li,
		li a {
			white-space: nowrap;
		}
	}
}
.slash {
	margin: 0 5px;
}
