.article-blog-section {
	padding: 75px 0;
	background-color: #f2f2f2;
	position: relative;
	.section-title {
		margin-bottom: 37px;
	}
	&__decoration {
		position: absolute;
		svg {
			width: 100%;
			height: 100%;
		}
		&_1 {
			width: 90px;
			height: 203px;
			bottom: 10px;
			right: 60px;
		}
		&_2 {
			width: 203px;
			height: 90px;
			top: 20%;
			left: 0;
		}
	}
	@include _mobile-tablet {
		&__decoration {
			display: none;
		}
	}
	@include _mobile {
		padding: 20px 0;
		.col-none {
			display: none;
		}
	}
	@include _tablet {
		padding: 40px 0;
	}
}

.article-blog-card {
	&__img {
		height: 190px;
		background-color: #f2f2f2;
		border-radius: 20px;
		overflow: hidden;
		position: relative;
		margin-bottom: 15px;
	}
	&__date {
		@extend %date-sticker;
		position: absolute;
		top: 20px;
		left: 0;
		z-index: 2;
	}
	.title-3 {
		margin-bottom: 12px;
		max-height: 47px;
		min-height: 47px;
		overflow: hidden;
	}
	&__href {
		color: $yellow;
		font-weight: bold;
		font-size: 14px;
		line-height: 1.5;
		text-decoration: none;
		display: flex;
		align-items: center;
		justify-content: flex-start;
		&:after {
			content: '';
			width: 5px;
			height: 10px;
			background-position: center center;
			background-size: 100% 100%;
			background-repeat: no-repeat;
			background-image: url(../img/arrow_yellow.svg);
			display: inline-flex;
			margin-left: 5px;
			transition: 0.3s all;
		}
		&:hover {
			&::after {
				transform: translateX(3px);
			}
		}
		/* identical to box height, or 150% */
	}
	@include _tablet {
		&__img {
			height: auto;
		}
		.title-3 {
			min-height: 36px;
			max-height: 36px;
			margin-bottom: 15px;
		}
	}

	@include _mobile {
		&__img {
			height: auto;
			margin-bottom: 5px;
		}
		.title-3 {
			margin-bottom: 0 !important;
			max-height: 48px;
			min-height: 48px;
		}
	}
}
