.category-section {
	position: relative;
	margin-bottom: 40px;
	.section-title {
		margin-bottom: 23px;
	}
	&_grey {
		padding: 70px 0 30px 0;
		background-color: #fbfbfb;
		margin-bottom: 95px;
		.category-section-card {
			background-color: #fff;
		}
	}
	&__decoration {
		position: absolute;
		svg {
			width: 100%;
			height: 100%;
		}
		&_1 {
			width: 200px;
			height: 90px;
			top: 200px;
			left: 0;
		}
		&_2 {
			width: 90px;
			height: 200px;
			right: 56px;
			bottom: 100px;
		}
	}

	@include _mobile-tablet {
		margin-bottom: 25px;
		&__decoration {
			display: none;
		}
		&_grey {
			padding: 40px 0 50px 0;
		}
	}
	@include _mobile {
		margin-bottom: 15px;

		&_grey {
			padding: 16px 0 30px 0;
		}
	}
}
