.partnership {
	position: relative;
	&__img {
		height: 220px;
		margin-bottom: 30px;
	}
	p {
		font-size: 20px;
		line-height: 1.8;
		/* or 180% */
		color: $black-text;
	}
	.title-3 {
		margin-bottom: 45px;
	}
	.section-title {
		margin-bottom: 36px;
	}
	&__form {
		padding: 30px 0 50px 0;
		margin-bottom: 0;
		margin-top: 70px;
		.section-title {
			color: #fff;
			margin-bottom: 22px;
		}
		.input {
			margin-bottom: 30px;
			input {
				padding: 15px 20px;
			}
		}
		.btn-primary {
			padding: 15px 20px;
		}
	}
	&__decoration {
		position: absolute;
		svg {
			width: 100%;
			height: 100%;
		}
		&_1 {
			width: 90px;
			height: 203px;
			bottom: 270px;
			right: 60px;
		}
		&_2 {
			width: 203px;
			height: 90px;
			top: 0;
			left: 0;
		}
	}

	@include _mobile-tablet {
		&__decoration {
			display: none;
		}
	}
	@include _mobile {
		.section-title {
			margin-bottom: 15px;
			padding-bottom: 0;
		}
		&__img {
			height: 85px;
			min-height: auto;
			margin-bottom: 16px;
			img {
				height: 100%;
				object-fit: cover;
			}
		}
		.title-3 {
			margin-bottom: 15px;
		}
		p {
			font-size: 14px;
			line-height: 17px;
			margin-top: 0;
		}
		&__form {
			margin-top: 30px;
			padding-top: 12px;
			padding-bottom: 20px;
			.input {
				margin-bottom: 10px;
				input {
					padding-top: 8px;
					padding-bottom: 8px;
				}
			}
			.btn-primary {
				padding: 8px 10px;
				min-width: auto;
				width: 100%;
			}
		}
	}

	@include _tablet {
		.section-title {
			margin-bottom: 10px;
			padding-bottom: 0;
		}
		&__img {
			height: auto;
			margin-bottom: 20px;
		}

		p {
			font-size: 18px;
			line-height: 17px;
			margin-top: 0;
			margin-bottom: 50px;
			/* or 97% */
		}
		&__form {
			.section-title {
				margin-bottom: 25px;
			}
			.btn-primary {
				margin-right: auto;
				margin-left: auto;
			}
		}
	}
}

.partnership-advantages {
	display: flex;
	align-items: flex-start;
	justify-content: space-between;
	flex-wrap: wrap;
	// @include _tablet {
	// }
}
.partnership-advantages-card {
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	text-align: center;
	width: 24%;
	&__icon {
		width: 57px;
		height: 57px;
		margin-bottom: 13px;
		svg {
			width: 100%;
			height: 100%;
			color: $yellow;
		}
	}
	.title-5 {
		margin-bottom: 10px;
	}
	&__text {
		@extend %paragraph-1;
	}
	@include _mobile-tablet {
		width: 46%;
		margin-bottom: 30px;
	}
	@include _mobile {
		margin-bottom: 25px;
		&__icon {
			width: 30px;
			height: 30px;
			margin-bottom: 5px;
		}
		&__text {
			font-size: 12px;
			line-height: 10px;
		}
	}
}
