.product-carousel {
	margin-bottom: 100px;
	.section-title {
		margin-bottom: 62px;
	}
	.owl-nav {
		top: 50%;
		left: -90px;
		width: calc(100% + 180px);
		button {
			border: 1px solid #c4c4c4 !important;
			border-radius: 6px;
			background-color: #fff !important;
			transition: 0.3s all;
			svg {
				color: #626262;
			}
			&:hover {
				background-color: $yellow !important;
				border-color: $yellow !important;
				svg {
					color: #fff;
				}
			}
		}
		// &:hover {
		// 	button {
		// 	}
		// }
	}
	@include _mobile {
		margin-bottom: 50px;
		.section-title {
			margin-bottom: 12px;
		}

		.owl-dots {
			bottom: -17px;
		}
	}
	@include _tablet {
		margin-bottom: 80px;
		.section-title {
			margin-bottom: 40px;
		}
		.owl-dots {
			bottom: -27px;
		}
	}
}
