.owl-nav {
	display: flex;
	align-items: center;
	justify-content: space-between;
	height: 0;
	width: calc(100% - 160px);
	left: 80px;
	position: absolute;
	top: 60%;
	transform: translateY(-50%);
	button {
		background-color: $yellow !important;
		display: flex;
		align-items: center;
		justify-content: center;
		border-radius: 50%;
		width: 60px;
		height: 60px;

		svg {
			color: #fff;
		}
	}
	@include _laptop {
		left: 30px;
		width: calc(100% - 60px);
	}
}

.owl-dots {
	position: absolute;
	bottom: 15px;
	left: 50%;
	transform: translateX(-50%);
	.owl-dot {
		width: 22px;
		height: 3px;
		background-color: #cbcbcb !important;
		margin-right: 7px;
		transition: 0.3s all;
		&:last-child {
			margin-right: 0;
		}
	}
	.active {
		background-color: $yellow !important;
	}
}
