.pagination {
	@extend %ul-reset;
	display: flex;
	align-items: center;
	justify-content: center;
	&__item {
		width: 50px;
		height: 50px;
		display: flex;
		align-items: center;
		justify-content: center;
		border: 1px solid #c4c4c4;
		border-radius: 6px;
		transition: 0.3s all;
		@extend %pagination-text;
		margin-right: 20px;
		&:last-child {
			margin-right: 0;
		}
		&_prev,
		&_next {
			display: flex;
			align-items: center;
			justify-content: center;
			svg {
				width: 12px;
				height: 6px;
				color: #626262;
			}
		}
		a {
			display: flex;
			align-items: center;
			justify-content: center;
			text-decoration: none;
			transition: 0.3s all;
			@extend %pagination-text;
			width: 100%;
			height: 100%;
		}

		&_active {
			background-color: $yellow;
			color: #fff;
			border-color: $yellow;
		}

		&_prev svg {
			transform: rotate(90deg);
		}
		&_next svg {
			transform: rotate(-90deg);
		}
		&:hover {
			background-color: $yellow;
			border-color: $yellow;
			a {
				color: #fff;
			}
		}
	}
	@include _mobile {
		&__item {
			width: 46px;
			min-width: 46px;
			height: 46px;
			margin-right: 14px;
		}
	}
}
