.content {
	margin-bottom: 80px;
	position: relative;
	z-index: 1;
	.section-title {
		margin-bottom: 40px;
	}
	&__img {
		position: relative;
		z-index: 1;
		&_margin-4 {
			margin-bottom: 40px;
		}
		&_left-decoration {
			&:after {
				content: '';
				position: absolute;
				z-index: -1;
				display: block;
				width: 90px;
				height: 330px;
				left: -15px;
				top: -13px;
				border: 1px solid $yellow;
			}
		}
		&_right-decoration {
			&::after {
				content: '';
				position: absolute;
				border: 1px solid $yellow;
				display: block;
				z-index: -1;
			}
			&-1 {
				&::after {
					right: -19px;
					top: -49px;
					width: 90px;
					height: 270px;
				}
			}
			&-2 {
				&::after {
					right: -32px;
					top: -13px;
					width: 90px;
					height: 326px;
				}
			}
			&-3 {
				&::after {
					right: -19px;
					top: -49px;
					width: 90px;
					height: 270px;
				}
			}
		}
		&_margin-top {
			margin-top: 120px;
			margin-bottom: 30px;
		}
		&_margin-1 {
			margin-bottom: 11px;
			margin-top: 40px;
		}
		&_margin-2 {
			margin-bottom: 25px;
			margin-top: 30px;
		}
	}
	.title-2 {
		a {
			color: $black;
			&:hover {
				text-decoration: none;
			}
		}
	}
	.title-3 {
		margin-bottom: 18px;
		a {
			color: $black;
			&:hover {
				text-decoration: none;
			}
		}
	}
	.title-4 {
		margin-bottom: 8px;
	}
	span {
		@extend %paragraph-1;
	}
	.ul-lines {
		margin-bottom: 6px;
		li {
			margin-bottom: 8px;
			&:last-child {
				margin-bottom: 0;
			}
		}
		&_margin-3 {
			margin-bottom: 30px;
		}
	}
	.ul-check {
	}
	&__decoration {
		position: absolute;
		z-index: -1;
		svg {
			width: 100%;
			height: 100%;
		}
		&_1 {
			left: 0;
			top: 3%;
			width: 203px;
			height: 89px;
		}
		&_2 {
			height: 203px;
			width: 89px;
			right: 2%;
			top: 18%;
		}
		&_3 {
			width: 18px;
			height: 89px;
			right: 2%;
			top: 30%;
		}
		&_4 {
			width: 18px;
			height: 89px;
			right: 2%;
			top: 50%;
		}
		&_5 {
			width: 203px;
			height: 315px;
			left: 0;
			top: 50%;
		}
	}
	@include _mobile {
		margin-bottom: 30px;
		&__decoration {
			display: none;
		}
		&__img {
			&_left-decoration::after {
				width: 47px;
				height: 172px;
				left: -7px;
			}
			&_right-decoration::after {
				width: 47px;
				height: 140px;
				top: -20px;
				right: -9px;
			}
			&_margin-4 {
				margin-bottom: 10px;
			}
			&_margin-top {
				margin-top: 0;
			}
			&_margin-2 {
				margin-top: 0;
			}
			&_margin-1 {
				margin-top: 0;
			}
		}
		ul {
			margin-bottom: 20px;
		}
	}
	@include _tablet {
		margin-bottom: 40px;
		&__decoration {
			display: none;
		}
		&__img {
			&_left-decoration::after {
				height: 200px;
				width: 60px;
			}
			&_right-decoration::after {
				height: 175px;
				width: 60px;
				top: -30px;
			}
		}
	}
	@include _laptop {
		&__decoration {
			display: none;
		}
	}
}
