.logo {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	text-decoration: none;
	svg {
		width: 41px;
		height: 41px;
		margin-right: 13px;
	}
	span {
		@extend %logo-text;
	}
	@include _mobile {
		min-width: 135px;
		svg {
			width: 27px;
			min-width: 27px;
			height: 27px;
			margin-right: 8px;
		}
	}
}
