.cost-calculator {
	margin-bottom: 100px;
	position: relative;
	.input {
		margin-bottom: 30px;
		input {
			padding: 17px 20px;
			border: 1px solid #cecece;
			width: 100%;
			border-radius: 4px;
			font-weight: bold;
			font-size: 14px;
			line-height: 1.4;
			/* or 139% */
		}
	}
	.section-title {
		margin-bottom: 37px;
	}
	.btn-primary {
		margin-right: auto;
		margin-left: auto;
	}
	&__decoration {
		position: absolute;
		svg {
			width: 100%;
			height: 100%;
		}
		&_1 {
			width: 90px;
			height: 203px;
			bottom: 15%;
			right: 60px;
		}
		&_2 {
			width: 203px;
			height: 90px;
			top: 50%;
			left: 0;
		}
	}
	&__img {
		position: absolute;
		height: calc(100% + 167px);
		right: 0;
		top: -66px;
		z-index: -1;
		img {
			object-fit: cover;
			height: 100%;
		}
	}
	@include _mobile {
		margin-bottom: 0;
		.btn-primary {
			margin-bottom: 30px;
		}
		&__decoration {
			display: none;
		}
		.custom-select {
			margin-bottom: 10px;
		}
		.input {
			margin-bottom: 10px;
			input {
				padding: 8px 10px;
			}
		}
		&__img {
			position: static;
			height: 125px;
			width: 100%;
			margin-bottom: 0;
			img {
				width: 100%;
			}
		}
	}
	@include _tablet {
		margin-bottom: 50px;
		.section-title {
			margin-bottom: 15px;
			padding-bottom: 0;
		}
		&__decoration {
			&_2 {
				display: none;
			}
		}
		&__img {
			max-width: 260px;
		}
		.input input {
			padding: 10px;
			min-height: 46px;
			max-height: 46px;
		}
	}
	@include _laptop {
		&__img {
			max-width: 400px;
		}
	}
	@include _large {
		&__img {
			max-width: 600px;
		}
	}
}
