.header-discount-form {
	padding: 70px 32px 40px 32px;
	background-color: #fbfbfb;
	box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.25);
	border-radius: 10px;
	position: fixed;
	right: 90px;
	bottom: 70px;
	z-index: 999;
	display: flex;
	align-items: center;
	justify-content: flex-start;
	flex-direction: column;
	transition: 0.4s all;
	transform: scale(1.1);
	opacity: 0;
	visibility: hidden;
	pointer-events: none;
	&_active {
		pointer-events: inherit;
		visibility: visible;
		opacity: 1;
		transform: scale(1);
	}
	&__sticker {
		position: absolute;
		width: 103px;
		min-width: 103px;
		height: 103px;
		border-radius: 50%;
		left: 50%;
		transform: translateX(-50%);
		top: -52px;
		display: flex;
		align-items: center;
		justify-content: center;
		font-weight: bold;
		font-size: 36px;
		line-height: 32px;
		&_yellow {
			background-color: $yellow;
			color: #fff;
		}
	}
	&__cls-btn {
		position: absolute;
		background-color: transparent;
		cursor: pointer;
		border: none;
		border: 2px solid rgba(0, 0, 0, 0.3);
		border-radius: 50%;
		display: flex;
		align-items: center;
		width: 39px;
		min-width: 39px;
		height: 39px;
		justify-content: center;
		top: 19px;
		right: 16px;
		svg {
			width: 18px;
			height: 18px;
			color: $grey;
		}
	}
	&__title {
		font-style: normal;
		font-weight: bold;
		font-size: 36px;
		line-height: 32px;
		text-align: center;
		color: #000;
		padding-bottom: 26px;
		position: relative;
		margin-bottom: 26px;

		&:after {
			content: '';
			width: 62px;
			height: 2px;
			background-color: $yellow;
			position: absolute;
			display: block;
			bottom: 0;
			left: 50%;
			transform: translateX(-50%);
		}
	}
	&__subtitle {
		max-width: 340px;
		font-weight: normal;
		font-size: 24px;
		line-height: 25px;
		margin-bottom: 38px;
		/* or 105% */
		text-align: center;
		font-feature-settings: 'pnum' on, 'lnum' on;
		color: #000000;
	}
	.input {
		width: 100%;
		margin-bottom: 20px;
		input {
			background-color: #fff;
			width: 100%;
		}
	}
	&__submit {
		padding: 20px 10px;
		min-width: 254px;
		border-radius: 4px;
		width: fit-content;
	}
	@include _tablet {
		right: 10px;
		bottom: 10px;
		padding-top: 40px;
		&__title {
			font-size: 24px;
		}
		&__subtitle {
			font-size: 18px;
		}
		&__sticker {
			width: 50px;
			min-width: 50px;
			height: 50px;
			font-size: 20px;
			line-height: 1;
			top: -20px;
		}
	}
	@include _mobile {
		padding: 10px;
		padding-top: 30px;
		bottom: 10px;
		right: 10px;

		&__title {
			font-size: 18px;
			line-height: 1;
			padding-bottom: 15px;
			margin-bottom: 15px;
		}
		&__sticker {
			width: 40px;
			min-width: 40px;
			height: 40px;
			font-size: 14px;
			line-height: 1;
			top: -15px;
		}
		&__subtitle {
			font-size: 14px;
			line-height: 1;
			max-width: 220px;
			margin-right: auto;
			margin-left: auto;
			margin-bottom: 15px;
			br {
				display: none;
			}
		}
		&__submit {
			padding: 10px;
		}
		&__cls-btn {
			top: 10px;
			right: 10px;
			width: 20px;
			min-width: 20px;
			height: 20px;
			padding: 0;
			svg {
				width: 10px;
				height: 10px;
			}
		}
	}
}
