@for $i from 1 to 5 {
	h#{$i} {
		@extend %title-#{$i};
	}
}
//titles by class
.title {
	@for $i from 1 to 7 {
		&-#{$i} {
			@extend %title-#{$i};
		}
	}
}

.section-title {
	@extend %title-1;
	position: relative;
	padding-bottom: 23px;
	&:after {
		content: '';
		position: absolute;
		bottom: 0;
		width: 62px;
		height: 3px;
		background-color: $yellow;
		left: 50%;
		transform: translateX(-50%);
	}
	&_no-line {
		padding: 0;
		&::after {
			content: none;
		}
	}
	&_left {
		text-align: left;
	}

	@include _tablet {
		margin-bottom: 30px;
		padding-bottom: 13px;
	}

	@include _mobile {
		padding-bottom: 10px;
		margin-bottom: 10px;
	}
}

.subtitle {
	@for $i from 1 to 3 {
		&-#{$i} {
			@extend %subtitle-#{$i};
		}
	}
}
