.product-card {
	padding: 18px 34px 25px 34px;
	display: flex;
	align-items: center;
	justify-content: flex-start;
	flex-direction: column;
	min-height: 360px;
	border: 1px solid #cecece;
	border-radius: 20px;
	&__img {
		width: 176px;
		height: 176px;
		display: flex;
		align-items: center;
		justify-content: center;
		margin-bottom: 17px;
	}
	&__title {
		@extend %product-card-title;
		display: flex;
		margin-bottom: 17px;
		line-height: 1.31;
		font-feature-settings: 'pnum' on, 'lnum' on;
		color: #000000;
		text-decoration: none;
		transition: 0.3s all;
		&:hover {
			color: $yellow;
		}
	}
	&__price {
		font-weight: bold;
		font-size: 24px;
		line-height: 0.87;
		/* identical to box height, or 87% */
		font-feature-settings: 'pnum' on, 'lnum' on;
		margin-bottom: 30px;
		color: #000000;
	}
	@include _mobile {
		padding: 11px 8px 18px 8px;
		min-height: auto;
		max-width: 200px;
		margin-left: auto;
		margin-right: auto;
		&__img {
			width: 93px;
			height: 93px;
			margin-bottom: 8px;
		}
		&__title {
			margin-bottom: 0;
			line-height: 21px;
			max-height: 21px;
			min-height: 21px;
			overflow: hidden;
		}
		&__price {
			margin-bottom: 10px;
			font-size: 18px;
			max-height: 15px;
			min-height: 15px;
			overflow: hidden;
		}

		.btn-primary {
			max-width: 100px;
			margin-right: auto;
			margin-left: auto;
		}
	}
	@include _tablet {
		padding: 15px 30px 25px 30px;
		min-height: auto;
		&__img {
			width: 141px;
			height: 141px;
		}
		&__price {
			margin-bottom: 20px;
			font-size: 20px;
		}
	}
}
