.info {
	&__price {
		display: flex;
		align-items: flex-end;
		justify-content: flex-start;
		padding-bottom: 15px;
		border-bottom: 1px solid #c4c4c4;
		margin-bottom: 31px;
	}
	&__item {
		display: flex;
		align-items: flex-start;
		justify-content: flex-start;
		margin-bottom: 25px;
	}
	&__key {
		@extend %info-key-text;
		margin-right: 5px;
	}
	&__value {
		@extend %info-value-text;
		&_red {
			color: $red;
		}
		&_big {
			font-size: 35px;
			line-height: 21px;
			font-weight: 800;
			margin-left: 20px;
		}
	}

	@include _mobile {
		&__price {
			width: fit-content;
			padding-bottom: 6px;
			margin-bottom: 20px;
		}
		&__item {
			margin-bottom: 20px;
		}
		&__value {
			&_big {
				font-size: 18px;
			}
		}

		.btn-primary {
			max-width: 400px;
			width: 100%;
		}
	}
}
