.characteristic-table {
	&__heading {
		background-color: #f8b006;
		border-radius: 10px 10px 0px 0px;
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 15px 30px;
	}

	&__heading-title {
		font-weight: bold;
		font-size: 20px;
		line-height: 1.05;
		font-feature-settings: 'pnum' on, 'lnum' on;
		color: #ffffff;
		&:nth-child(2) {
			max-width: 150px;
			width: 100%;
		}
	}
	&__item {
		padding: 15px 30px;
		display: flex;
		align-items: center;
		justify-content: space-between;
		&_biege {
			background-color: #f2f2f2;
		}
	}
	&__key,
	&__value {
		font-size: 20px;
		line-height: 21px;
		font-feature-settings: 'pnum' on, 'lnum' on;
		width: 100%;
		color: $black-text;
	}
	&__key {
		max-width: 270px;
		font-weight: 500;
		/* identical to box height, or 105% */
		font-feature-settings: 'pnum' on, 'lnum' on;
	}
	&__value {
		max-width: 150px;
		font-weight: bold;
		/* identical to box height, or 105% */
	}

	&__wrapper {
		border: 1px solid #d3d3d3;
		border-top: none;
	}

	@include _mobile {
		&__heading {
			padding: 5px 10px;
		}
		&__heading-title {
			font-size: 12px;
			max-width: none;
			width: 70%;
			&:nth-child(2n) {
				width: 30%;
			}
		}
		&__key,
		&__value {
			font-size: 12px;
			max-width: none;
		}
		&__key {
			width: 70%;
		}
		&__value {
			width: 30%;
		}
		&__item {
			padding: 3px 10px;
		}
	}
	@include _tablet {
		&__heading-title {
			font-size: 18px;
			line-height: 21px;
		}
		&__key,
		&__value {
			font-size: 18px;
		}
		// &__value {
		// }
	}
}
