.footer {
	padding: 55px 0 75px 0;
	background-color: #181818;
	position: relative;
	&__title {
		@extend %footer-title;
		margin-bottom: 5px;
	}
	.logo {
		margin-bottom: 20px;
		span {
			color: #fff;
		}
	}
	&__row {
		display: flex;
		align-items: flex-start;
		justify-content: flex-start;
	}
	&__wrapper {
		margin-top: 61px;
		&_padding {
			// padding-left: 45px;
		}
		&_contacts {
			display: flex;
			flex-direction: column;
			padding-left: 10px;
		}
	}
	&__contacts-text,
	&__contacts-href {
		@extend %footer-text;
		text-decoration: none;
		color: #fff;
	}
	&__contacts-href {
		font-feature-settings: 'pnum' on, 'lnum' on;
	}
	&__bottom {
		background-color: $yellow;
		padding: 13px 0 12px 0;
		.container {
			display: flex;
			align-items: center;
			justify-content: space-between;
		}
	}
	&__copyright {
		font-weight: normal;
		font-size: 14px;
		line-height: 1.43;
		/* identical to box height, or 143% */
		text-align: center;
		color: #ffffff;
	}
	&__dev {
		display: flex;
		align-items: center;
		justify-content: flex-start;
	}
	&__dev-caption {
		font-weight: normal;
		font-size: 14px;
		line-height: 1.43;
		/* identical to box height, or 143% */
		color: #ffffff;
		margin-right: 34px;
	}
	&__dev-logo {
		width: 87px;
		height: 15px;
		svg {
			width: 100%;
			height: 100%;
		}
	}
	.social {
		margin-top: 20px;
		top: 65px;
	}
	@include _mobile {
		padding: 60px 0 30px 0;
		&__wrapper {
			padding-left: 0;
			margin-bottom: 20px;
			margin-top: 20px;
			&_contacts {
				margin-top: 0;
			}
		}
		&__row {
			flex-wrap: wrap;
		}
		.social {
			position: absolute;
			right: 15px;
			margin-top: 0;
			top: 60px;
		}
	}
	@include _tablet {
		padding: 50px 0;
		&__title {
			&_top-margin {
				margin-top: 15px;
			}
		}
		&__wrapper {
			padding-left: 0;
			margin-top: 35px;
			&_contacts {
				margin-top: 0;
			}
		}
	}
}

.footer-navigation {
	@extend %ul-reset;
	margin-right: 30px;
	&__link {
		@extend %footer-text;
		text-decoration: none;
		transition: 0.3s all;
		&:hover {
			color: $yellow;
		}
	}
	@include _mobile {
		width: 50%;
		margin-right: 0;
	}
}
