.social {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	max-width: 95px;
	border-radius: 10px;
	padding: 10px 20px;
	&_black {
		background-color: #363636;
	}
	&_white {
		background-color: #f2f2f2;
	}
	&__item {
		width: 20px;
		height: 20px;
		text-decoration: none;
		margin-right: 15px;
		&:last-child {
			margin-right: 0;
		}
		svg {
			width: 100%;
			height: 100%;
		}
	}
	@include _mobile {
		&_white {
			padding: 6px 20px;
		}
	}
}
