.characteristic-table {
	border-collapse: collapse; /*      */
	width: 100%;
	margin-bottom: 36px;
	th {
		@extend %card-title-1;
		background-color: $yellow;
		color: #ffffff;
		padding: 9px 10px;

		&:first-child {
			border-radius: 10px 0 0 0;
			overflow: hidden;
		}
		&:last-child {
			border-radius: 0 10px 0 0;
			overflow: hidden;
		}
	}
	td {
		@extend %title-3;
		font-feature-settings: 'pnum' on, 'lnum' on;
		border: 1px solid $border-color;
		text-align: center;
		color: $black-text;
		padding: 16.5px 10px;
	}

	@include _mobile {
		th {
			font-size: 12px;
		}
		td {
			font-size: 12px;
		}
	}
}
