.input {
	input {
		padding: 20px;
		@extend %input-text;
		text-align: left;
		background-color: #ffffff;
		border: none;
		outline: none;
		border-radius: 4px;
		width: 100%;
		font-feature-settings: 'pnum' on, 'lnum' on;
	}
	@include _mobile {
		input {
			padding: 9px 8px;
		}
	}
	@include _tablet {
		input {
			padding: 15px;
		}
	}
}
//
.checkbox {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	align-content: flex-start;
	position: relative;
	cursor: pointer;
	user-select: none;
	margin-bottom: 10px;
	input {
		display: none;
		opacity: 0;
		visibility: hidden;
	}
	.checkmark {
		border-radius: 2px;
		display: flex;
		align-items: center;
		justify-content: center;
		align-content: center;
		margin-right: 13px;
		position: relative;
		border: 1px solid #cecece;
		background-color: #fff;
		border-radius: 4px;
		top: 0;
		left: 0;
		height: 26px;
		width: 26px;
		min-width: 14px;
		transition: 0.3s all;

		&::after {
			content: '';
			display: block;
			position: absolute;
			width: 18px;
			height: 18px;
			background-repeat: no-repeat;
			background-position: center center;
			background-size: 100% 100%;
			background-image: url('../img/check.svg');
			opacity: 0;
			visibility: hidden;
			transition: 0.3s all;
		}
	}
	&__text {
		@extend %checkbox-text;
		color: #000;
	}
	&__caption {
		@extend %checkbox-caption;
		margin-left: auto;
	}

	input:checked ~ .checkmark {
		// background-color: $orange;
		// border-color: $orange;
	}
	input:checked ~ .checkmark:after {
		opacity: 1;
		visibility: visible;
	}

	@include _mobile {
		&__text {
			font-size: 16px;
		}
	}
}
//  custom selects

/* The container must be positioned relative: */
.custom-select {
	position: relative;
	margin-bottom: 30px;
	select {
		display: none;
	}
	.select-selected {
		background-color: #fff;
		padding: 17px 20px;
		border: 1px solid #cecece;
		width: 100%;
		border-radius: 4px;
		font-weight: bold;
		font-size: 14px;
		line-height: 1.4;
		color: #000;
		/* or 139% */
		position: relative;
		z-index: 98;
		&:after {
			content: '';
			width: 12px;
			height: 10px;
			background-repeat: no-repeat;
			background-position: center center;
			// background-size: cover;
			background-image: url(../img/grey-arrow.svg);
			position: absolute;
			right: 10px;
			top: 20px;
			transition: 0.3s all;
		}
	}
	.select-items {
		color: #000;
		border-radius: 4px;
		font-weight: bold;
		font-size: 14px;
		line-height: 1.4;
		color: #000;
		position: absolute;
		/* or 139% */
		top: 100%;
		left: 0;
		right: 0;
		z-index: 100;
		cursor: pointer;
		background-color: #fff;
		border-radius: 0 0 4px 4px;
		height: 200px;
		overflow-y: auto;
		border: 1px solid #cecece;
		> div {
			padding: 17px 20px;
		}
	}
	.select-arrow-active {
		z-index: 100;
		&::after {
			transform: rotate(180deg);
		}
	}
	@include _mobile {
		.select-selected {
			padding: 8px 10px;
			&::after {
				top: 12px;
			}
		}
	}
	@include _tablet {
		.select-selected {
			padding: 8px 10px;
			padding-right: 15px;
			line-height: 14px;
			// padding-right: 15px;
			min-height: 46px;
			max-height: 46px;
			&::after {
				right: 5px;
				top: 14px;
			}
		}
	}
}

/* Hide the items when the select box is closed: */
.select-hide {
	display: none;
}

.select-items div:hover,
.same-as-selected {
	background-color: rgba(0, 0, 0, 0.1);
}
