.shipping {
	padding: 35px 57px 35px 63px;
	background-color: #f2f2f2;
	border-radius: 20px;

	&__title {
		display: flex;
		align-items: center;
		justify-content: space-between;
		font-weight: bold;
		font-size: 16px;
		line-height: 1.31;
		/* identical to box height, or 131% */
		color: $black-text;
		margin-bottom: 10px;
	}
	&__icon {
		width: 30px;
		height: 30px;
		svg {
			width: 100%;
			height: 100%;
			color: $yellow;
		}
	}
	&__text {
		margin-bottom: 10px;
		font-weight: normal;
		font-size: 16px;
		line-height: 1.31;
		/* or 131% */
		color: $black-text;
		span {
			display: block;
		}
		p {
			margin-top: 0;
			&:last-child {
				margin-bottom: 0;
			}
		}
	}
	&__section {
		margin-bottom: 20px;
		&:last-child {
			margin-bottom: 0;
		}
	}

	@include _mobile {
		padding: 16px 15px 20px 21px;
		max-width: 500px;
		margin-bottom: 22px;
		&__section {
			max-width: 400px;
			margin-bottom: 15px;
		}

		&__title {
			font-size: 14px;
			line-height: 21px;
		}
		&__text {
			font-size: 12px;
		}
	}

	@include _tablet {
		padding: 20px 45px 20px 32px;
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
		max-width: 750px;
		width: 100%;
		margin-bottom: 40px;
		&__section {
			max-width: 226px;
			width: 100%;
			margin-bottom: 0;
			&:last-child {
				max-width: 300px;
			}
		}
	}
}
