.sort {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	position: relative;
	&__caption {
		@extend %paragraph-1;
		margin-right: 15px;
	}
	&__toggle {
		padding: 2px 5px;
		border: 1px solid #cecece;
		display: flex;
		align-items: center;
		justify-content: flex-start;
		font-weight: normal;
		font-size: 12px;
		line-height: 1.75;
		/* identical to box height, or 175% */
		color: #adadad;
		cursor: pointer;
		border-radius: 4px;
		svg {
			min-width: 10px;
			width: 10px;
			height: 6px;
			margin-left: 5px;
			transition: 0.3s all;
		}
		&_active {
			svg {
				transform: rotate(180deg);
			}
		}
	}
	&__dropdown {
		position: absolute;
		top: 100%;
		border: 1px solid #cecece;
		left: 0;
		opacity: 0;
		visibility: hidden;
		pointer-events: none;
		width: 100%;
		border-radius: 4px;
		background-color: #fff;
		transition: 0.3s all;
		&_active {
			opacity: 1;
			visibility: visible;
			pointer-events: inherit;
		}
	}
	&__item {
		padding: 5px;
		font-size: 12px;
		line-height: 1.75;
		color: #adadad;
		cursor: pointer;
	}

	@include _mobile {
		flex-direction: column;
		&__caption {
			margin-right: 0;
			margin-bottom: 7px;
		}
	}
}
